.sb-markdown {
  a {
    color: var(--sbt-blue-60);

    &:hover {
      color: var(--sbt-blue-80);
    }
  }
}

.sb-markdown__content {
  ul,
  ol {
    list-style-position: outside;
    margin-bottom: 0.75rem;
    margin-left: 1.75rem;
  }

  ul {
    list-style-type: disc;
  }

  ol {
    list-style-type: decimal;
  }

  p {
    font-size: var(--sbt-font-size-30);
    line-height: var(--sbt-line-height-50);
    margin-bottom: 0.75rem;
    margin-top: 0;
  }

  a {
    font-weight: var(--sbt-font-weight-60);
    text-decoration: none;
    text-transform: lowercase;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 0.5rem;
  }

  h1,
  h2,
  h3 {
    font-weight: var(--sbt-font-weight-70);
    margin-top: 1.25rem;
  }

  h1 {
    font-size: var(--sbt-font-size-40);
  }

  h2 {
    font-size: var(--sbt-font-size-35);
  }

  h3 {
    font-size: var(--sbt-font-size-30);
  }

  h4 {
    color: var(--sbt-theme-tertiary-color-50);
    font-size: var(--sbt-font-size-25);
    font-weight: var(--sbt-font-weight-70);
    margin-top: 0.75rem;
  }

  blockquote {
    border-left: 3px solid var(--sbt-theme-tertiary-color-30);
    margin-bottom: 0.75rem;
    margin-left: 1.25rem;
    padding-left: 1.25rem;
  }

  pre {
    background-color: var(--sbt-theme-tertiary-color-10);
    border: 1px solid var(--sbt-theme-tertiary-color-40);
    margin-bottom: 0.75rem;
    padding: 15px 1.25rem;
  }
}
